<template>
    <div class="help">
        <div class="window light">
            <p class="window-title">{{$localizeFilter('help')}}</p>
            <p class="window-text">{{$localizeFilter('help_text_1')}}</p>
            <ol>
                <li>
                    <div class="button-block">
                        <p class="window-text" style="line-height: 30px;">{{$localizeFilter('help_text_2')}} <span style="font-weight:700;">{{'Start'}}</span></p>
                        <button class="basic-button" @click="openBot">{{$localizeFilter('help_button')}}<img class="telegram-image" src="@/assets/newImages/telegram.png"></button>
                    </div>
                </li>
                <li>
                    <p class="window-text">{{$localizeFilter('help_text_4_1')}} <span style="font-weight:700;">{{$localizeFilter('help_text_4_2')}}</span></p>
                    <div class="input-field disabled">
                        <input id="code" class="custom-input" type="text" v-model.trim="code"/>
                        <button class="custom-input-button copy-button" @click="copy(code)"><i class="material-icons">content_copy</i></button>
                    </div>
                </li>
                <li>
                    <p class="window-text">{{$localizeFilter('help_text_5')}}</p>
                </li>
            </ol>
      </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    data:  () => ({
        code: '',
        botLink: ''
    }),
    components: {
    },
    methods: {
        openBot() {
            window.open(this.botLink)
        },
        copy(value) {
            function fallbackCopyTextToClipboard(text) {
                var textArea = document.createElement("textarea");
                textArea.value = text;
                
                // Avoid scrolling to bottom
                textArea.style.top = "0";
                textArea.style.left = "0";
                textArea.style.position = "fixed";

                document.body.appendChild(textArea);
                textArea.focus();
                textArea.select();
                var successful;
                try {
                    successful = document.execCommand('copy');
                    var msg = successful ? 'successful' : 'unsuccessful';
                    // console.log('Fallback: Copying text command was ' + msg);
                } catch (err) {
                    console.error('Fallback: Oops, unable to copy', err);
                }

                document.body.removeChild(textArea);
                return successful;
            }
            if (value) {
                const clipboardData =
                // event.clipboardData ||
                window.clipboardData ||
                // event.originalEvent?.clipboardData ||
                navigator.clipboard;
                try {
                    clipboardData.writeText(value); 
                    this.$message(localizeFilter('copied')) 
                } catch (e) {
                    if(fallbackCopyTextToClipboard(value)){
                        this.$message(localizeFilter('copied')) 
                    }
                }
            }
      }

    },
    async mounted() {
        try {
            const help = await this.$store.dispatch('getHelp')
            this.code = help.helpToken
            this.botLink = help.helpTelegramBotLink
        } catch(e) {
            this.$error(e)
        } 
    }
})
</script>

